$primary: #33C7EF;
$secondary: #37F093; 
$container: 96px;
html { 
  font: normal normal normal 1rem/1.6 'Nunito', -apple-system, BlinkMacSystemFont, Helvetica Neue, Helvetica, Arial, sans-serif;
  color: #2d2d2d;
  height: 100%;
  -webkit-font-smoothing: antialiased;
}

body {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5 {
  line-height: 1.2;
  margin-top: 0;
  margin-bottom: 1rem;
  font-weight: 400;
  font-family: 'Paytone One';
}

a {
  color: $primary;
  font-weight: 700;
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  }
}

.container {
  max-width: 1000px;
  padding: 0 1rem;
  margin: 0 auto;
}

.hero-container {
  padding: 60px 0;
  max-width: 550px;
  h1 {
    margin-bottom: 0;
    font-weight: 700;
    font-size: 3.5rem;
  }
  p {
    font-size: 1.5rem;
    color: #666;
    margin-bottom: 3rem;
  }
  .subtitle {
    font-weight: 700;
    font-size: 2rem;
  }
}

.site-footer {
  background: #2D2D2D;
}

a.button,
a.button-secondary {
  -webkit-appearance: none;
  transition: background 500ms ease;
  background-image: linear-gradient(to right, $primary 0%, $secondary 51%, $primary 100%);
  background-size: 300% auto; //background: linear-gradient(to top right, $primary 80%, $secondary 20%);
  color: white; //border: 6px solid white;
  border-radius: 40px;
  text-align: center;
  vertical-align: middle;
  padding: 1.25rem 1.75rem;
  font-size: 1.1rem;
  box-shadow: 2px 3px 5px rgba(0, 0, 0, .05);
  &:hover {
    text-decoration: none;
    background-position: right center;
  }
}

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1000px;
  margin: 0 auto;
  height: 80px;
  font-size: 1.1rem;
}

.brand {
  a {
    font-weight: 400;
    color: #2d2d2d;
    background: linear-gradient(180deg, transparent 55%, lighten($primary, 30%) 0);
    text-decoration: none;
    &:hover {
      background: linear-gradient(180deg, transparent 55%, lighten($secondary, 30%) 0);
    }
  }
}

.site-navigation {
  height: 80px;
  padding: 0 1rem;
  border-bottom: 1px solid #efefef;
  font-family: 'Nunito';
  .menu a {
    color: #888;
    font-weight: 400;
    margin: 0 2px;
    padding: 1rem;
    &:hover {
      text-decoration: none;
      color: #111;
    }
  }
}